import React from "react";
import ReactDOM from "react-dom";
import message from "@davistran86/notification";
import ReactLive from "./Component/ReactLive";
import styled from "styled-components";

import theme from "./utils/theme";
import { Header, Title, SubTitle, Button, Hr } from "./styles";
import "./styles.css";

import * as command from "./utils/commands";

const scope = { styled, message, Button };

const content = "Lorem ipsum dolor sit amet, consectetur adipiscing.";
const customContent = {
  title: "Lorem ipsum dolor sit amet",
  description:
    "Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur."
};
function App() {
  return (
    <div className="App">
      <Header>Simple Notification</Header>
      <Hr />
      <Title>Notification with White Background</Title>
      <SubTitle>
        Click on the bellow buttons to see the notification in action or make
        change directly to the code to try different configuations
      </SubTitle>
      <ReactLive
        code={command.message({ content, type: "success" })}
        scope={scope}
        theme={theme}
      />
      <ReactLive
        code={command.message({ content, type: "error" })}
        scope={scope}
        theme={theme}
      />
      <ReactLive
        code={command.message({ content, type: "info" })}
        scope={scope}
        theme={theme}
      />
      <ReactLive
        code={command.message({ content, type: "warning" })}
        scope={scope}
        theme={theme}
      />
      <ReactLive
        code={command.message({ content, type: "open" })}
        scope={scope}
        theme={theme}
      />
      <Hr />
      <Title>Notification with Colored Background</Title>
      <SubTitle>
        Click on the bellow buttons to see the notification in action or make
        change directly to the code to try different configuations
      </SubTitle>
      <ReactLive
        code={command.message({
          content,
          type: "success",
          option: { bold: true }
        })}
        scope={scope}
        theme={theme}
      />
      <ReactLive
        code={command.message({
          content,
          type: "error",
          option: { bold: true }
        })}
        scope={scope}
        theme={theme}
      />
      <ReactLive
        code={command.message({
          content,
          type: "info",
          option: { bold: true }
        })}
        scope={scope}
        theme={theme}
      />
      <ReactLive
        code={command.message({
          content,
          type: "warning",
          option: { bold: true }
        })}
        scope={scope}
        theme={theme}
      />
      <Hr />
      <Title>Notification with custom Title and Content</Title>
      <SubTitle>
        Click on the bellow buttons to see the notification in action or make
        change directly to the code to try different configuations
      </SubTitle>
      <ReactLive
        code={command.message({
          content: customContent,
          type: "success",
          option: { bold: true }
        })}
        scope={scope}
        theme={theme}
      />
      <Hr />
      <Title>Notification with custom Position</Title>
      <SubTitle>
        Click on the bellow buttons to see the notification in action or make
        change directly to the code to try different configuations
      </SubTitle>
      <ReactLive
        code={command.message({
          content,
          type: "success",
          option: { position: "topLeft" }
        })}
        scope={scope}
        theme={theme}
      />
      <ReactLive
        code={command.message({
          content,
          type: "error",
          option: { position: "topRight" }
        })}
        scope={scope}
        theme={theme}
      />
      <ReactLive
        code={command.message({
          content,
          type: "info",
          option: { position: "bottomLeft" }
        })}
        scope={scope}
        theme={theme}
      />
      <ReactLive
        code={command.message({
          content,
          type: "warning",
          option: { position: "bottomRight" }
        })}
        scope={scope}
        theme={theme}
      />
      <Hr />
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
