export const message = ({ type = "success", content = "", option = null }) => `
      <Button onClick={() => message.${type}(${
  typeof content === "string"
    ? `"${content}"`
    : `${JSON.stringify(content).replace(/"([^(")"]+)":/g, "$1:")}`
}${
  option ? `,${JSON.stringify(option).replace(/"([^(")"]+)":/g, "$1:")}` : ""
})}>${type === "open" ? "no icon" : type}</Button>
`;
