import styled from "styled-components";

export const Button = styled.button`
  min-width: 15%;
  padding: 1rem;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 4px;
  margin: 0.5rem;
  background: #ea5455;
  cursor: pointer;
  :active,
  :focus {
    outline: none;
  }
  :hover {
    background: d;
  }
`;

export const Header = styled.h1`
  font-size: 1.5rem;
  font-weight: bold;
  text-align: center;
  margin-top: 5rem;
`;

export const Title = styled.p`
  font-size: 1.3rem;
  font-weight: bold;
  margin: 0;
`;

export const SubTitle = styled.p`
  font-size: 1rem;
  margin-bottom: 2rem;
`;

export const ReactLiveWrapper = styled.div`
  padding: 0.5rem;
  background: #f4f5f7;
  border-radius: 4px;
  margin: 1rem 0;
  box-shadow: rgba(9, 30, 66, 0.31) 0px 0px 1px,
    rgba(9, 30, 66, 0.3) 0px 8px 16px -8px;
  .live-preview {
    background: #eeeeee;
    border-radius: 4px 4px 0 0;
  }
  .live-editor {
    font-size: 16px;
    text-align: left;
    textarea {
      background: #fff !important;
    }
  }
`;

export const Hr = styled.hr`
  border: 5px dotted #ea5455;
  border-style: none none dotted;
  color: #fff;
  background-color: #fff;
  width: 50px;
  margin: 5rem auto;
`;
